

















































import Vue from 'vue';
import { Format, Param, BOACSV, USBCSV } from '@/Format';

export default Vue.extend({
  data: () => ({
    dialog: false,
    format: '',
    formats: [BOACSV, USBCSV] as Format[],
    params: new Map<string, Param>(),
    fileReader: new FileReader(),
  }),
  methods: {
    readFile() {
      const format = this.getFormat(this.format) as Format;

      this.$store.commit('addRecord', {history: format.handler(this.fileReader.result as string, this.params)});

      this.dialog = false;
    },
    getFormat(name: string): Format | undefined {
      for (const format of this.formats) {
        if (format.name === name) {
          return format;
        }
      }
      return undefined;
    },
    hasParams(format: Format): boolean {
      return format !== undefined && format.params !== undefined && format.params.size > 0;
    },
    updateParam(name: string, value: any) {
      if (this.params.get(name) === undefined) {
        this.params.set(name,
          ((this.getFormat(this.format) as Format).params as Map<string, Param>).get(name) as Param);
      }
      (this.params.get(name) as Param).value = value;
    },
  },
});
