

























import Vue from 'vue';
import { History, Category } from '@/History';

export default Vue.extend({
  data: () => ({
    categories: Object.keys(Category).map((k) => Category[k as keyof typeof Category]),
  }),
  methods: {
    computeCategoryTotal(category: Category): number {
      let total = 0;
      for (const item of this.$store.state.theRecord.history) {
        if (item.category === category) {
          total += item.amount;
        }
      }

      return total;
    },
    getTotalSpent(): number {
      let total = 0;
      for (const item of this.$store.state.theRecord.history) {
        if (item.category !== Category.Income) {
          total += item.amount;
        }
      }

      return total;
    },
    getTotal(): number {
      let total = 0;
      for (const item of this.$store.state.theRecord.history) {
        total += item.amount;
      }

      return total;
    },
  },
});
