
















import Vue from 'vue';
import appFileReader from '@/components/FileReader.vue';

export default Vue.extend({
  name: 'App',
  components: {
    appFileReader,
  },
  data: () => ({
  }),
  methods: {
    loadFile(file: File) {
      const reader = new FileReader();
      reader.onload = () => {
        this.$store.commit('initRecord', reader.result as string);
      };

      reader.readAsText(file);
    },
  },
});
