import { History, Category } from '@/History';

export interface Format {
  name: string;
  handler: (content: string, params?: Map<string, Param>) => History[];
  params?: Map<string, Param>;
}

export interface Param {
  name: string;
  value: any;
  type: any;
}

export const BOACSV: Format = {
  name: 'Bank Of America CSV',
  handler: (csv: string) => {
    const parseResult: History[] = [];

    const table: string[][] = [];
    for (const [index, row] of csv.split('\r\n').entries()) {
      table[index] = [];

      const regex = /"([^"]+)"[,]?|([^,]+)[,]?/g;
      let result = regex.exec(row);
      let count = 0;
      while (result !== null) {
        table[index][count++] = result[1] === undefined ? result[2] : result[1];
        result = regex.exec(row);
      }
    }

    for (let i = 7; i < table.length - 1; i++) {
      const regex = /(\d+)\/(\d+)\/(\d+)/;
      const result = regex.exec(table[i][0]);
      const date = result === null ? new Date() : new Date(Number(result[3]), Number(result[1]), Number(result[2]));
      const amount = Number(table[i][2]);

      parseResult.push({
        date,
        numericDate: date.getTime(),
        description: table[i][1],
        amount,
        category: amount > 0 ? Category.Income : undefined,
      });
    }

    return parseResult;
  },
};

export const USBCSV: Format = {
  name: 'US Bank CSV',
  handler: (csv: string, params?: Map<string, Param>) => {
    const parseResult: History[] = [];

    // Add initial value history
    parseResult.push({
      date: new Date(0),
      numericDate: 0,
      description: '초기 금액',
      amount: ((params as Map<string, Param>).get('초기 금액') as Param).value,
      category: Category.Income,
    });

    const table: string[][] = [];
    for (const [index, row] of csv.split('\n').entries()) {
      table[index] = [];

      const regex = /"([^"]+)"[,]?/g;
      let result = regex.exec(row);
      let count = 0;
      while (result !== null) {
        table[index][count++] = result[1];
        result = regex.exec(row);
      }
    }

    for (let i = 1; i < table.length - 1; i++) {
      const regex = /(\d+)\/(\d+)\/(\d+)/;
      const result = regex.exec(table[i][0]);
      const date = result === null ? new Date() : new Date(Number(result[3]), Number(result[1]), Number(result[2]));
      const amount = Number(table[i][4]);

      parseResult.push({
        date,
        numericDate: date.getTime(),
        description: table[i][2],
        amount,
        category: amount > 0 ? Category.Income : undefined,
      });
    }

    return parseResult;
  },
  params: new Map([[
    '초기 금액', {
      name: '초기 금액',
      value: 0,
      type: Number,
    } as Param,
  ]]),
};
