import Vue from 'vue';
import Vuex from 'vuex';
import { History, Category } from '@/History';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    theRecord: {
      history: [] as History[],
    },
  },
  mutations: {
    addRecord(state, payload: { history: History[] }) {
      state.theRecord.history.push(...payload.history);
    },
    changeCategory(state, payload: { index: number, category: Category }) {
      state.theRecord.history[payload.index].category = payload.category;
    },
  },
  actions: {

  },
});
