














import Vue from 'vue';
import { History, Category } from '@/History';
import appTable from '@/components/Table.vue';
import appSummary from '@/components/Summary.vue';

export default Vue.extend({
  components: {
    appTable,
    appSummary,
  },
  data: () => ({
    csvFile: undefined as any,
  }),
  methods: {
    exportCSV() {
      let csv = '';
      let total = 0;
      const keys = Object.keys(Category);
      const totals = [] as number[];
      let totalSpent = 0;

      for (let i = 0; i < keys.length; i++) {
        totals[i] = 0;
      }

      // Header
      csv += '날짜,설명,비고,';
      for (const category of keys.map((k) => Category[k as keyof typeof Category])) {
        csv += `${category},`;
      }
      csv += '잔액\n';

      // Body
      for (const it of this.$store.state.theRecord.history) {
        const item = it as History;
        total += item.amount;
        if (item.category !== Category.Income) {
          totalSpent += item.amount;
        }

        csv += `${item.date.getFullYear()}/${item.date.getMonth()}/${item.date.getDate()},`;
        csv += `"${item.description}",,`;

        for (let i = 0; i < keys.length; i++) {
          if (Category[keys[i] as keyof typeof Category] === item.category) {
            csv += `${item.amount.toFixed(2)},`;
            totals[i] += item.amount;
          } else {
            csv += ',';
          }
        }

        csv += `${total.toFixed(2)}\n`;
      }

      // Footer
      csv += ',,,';
      for (const i of totals) {
        csv += `${i.toFixed(2)},`;
      }
      csv += '\n';
      csv += `지출 합계,${totalSpent.toFixed(2)},`;
      csv += `잔고,${total.toFixed(2)}`;

      if (this.csvFile !== undefined) {
        window.URL.revokeObjectURL(this.csvFile);
      }

      const blob = new Blob([csv], {type: 'text/csv'});

      this.csvFile = window.URL.createObjectURL(blob);

      const link = document.createElement('a');
      link.setAttribute('download', 'record.csv');
      link.href = this.csvFile;
      document.body.appendChild(link);

      // wait for the link to be added to the document
      window.requestAnimationFrame(() => {
        const event = new MouseEvent('click');
        link.dispatchEvent(event);
        document.body.removeChild(link);
      });
    },
  },
});
