





















import Vue from 'vue';
import { Category, History } from '@/History';

export default Vue.extend({
  data: () => ({
    headers: [
      {
        text: '날짜',
        value: 'numericDate',
        align: 'right',
      },
      { text: '설명', value: 'description' },
      { text: '분류', value: 'category' },
      {
        text: '금액',
        value: 'amount',
        align: 'right',
      },
    ],
    categories: Object.keys(Category).map((k) => Category[k as keyof typeof Category]),
  }),
  methods: {
    updateCategory(category: string, item: History) {
      for (const [index, i] of this.$store.state.theRecord.history.entries()) {
        if (item.date === i.date && item.description === i.description && item.amount === i.amount) {
          this.$store.commit('changeCategory', { index, category: category as Category });
        }
      }
    },
  },
});
